import { validateEmail } from '../../../shared/services';
import get from 'lodash/get';

export const getRequestClientName = (item: any, field = 'requesting_company') => {
	if (validateEmail(item[field].name)) {
		const { first_name, last_name } = get(item, `${field}_owner`);
		return `${first_name} ${last_name}`;
	}
	return get(item, `${field}.name`);
};
