import React from 'react';
import { useAppState } from 'shared/state';
import { usePagination } from 'shared/hooks';
import {
	KntButton,
	Pagination,
	TableBody,
	TableContainer,
	TableHead
} from 'shared/components/Elements';
import EntityContainer from 'modules/entity/containers';
import {
	ConfirmModal,
	Loader,
	StyledTable,
	StyledTableCell,
	StyledTableHeadCell,
	StyledTableRow
} from 'shared/components';
import { IncomingRequestType } from 'shared/typings/requests.type';
import { getRequestClientName } from '../../../utils/getRequestClientName';
import { useIncomingRequests } from '../../../hooks';
import { useSelector } from 'react-redux';
import { IRootState } from '../../../../../shared/interfaces';
import { getCompany } from '../../../../../modules/user/selectors';
import { useAuth } from '../../../../../modules/auth/hooks/useAuth';
import clsx from 'clsx';
import { useTranslation } from 'react-i18next';

type Props = {
	searchQuery: string;
};

export function IncomingRequests({ searchQuery }: Props) {
	const { t } = useTranslation();
	const company = useSelector((reduxState: IRootState) => getCompany(reduxState));
	const { companyId } = useAppState();
	const { page, handlePage } = usePagination();
	const { userData } = useAuth();

	const isOwner = company?.company?.owner?.id === userData?.id;

	const {
		rejectVisible,
		showRejectVisible,
		rejectLoading,
		closeRejectPopup,
		editVisible,
		showEditVisible,
		editLoading,
		closeEditPopup,
		handleStatusChange
	} = useIncomingRequests();

	const handleReject = () => () => handleStatusChange(rejectVisible!.id, 'reject');
	const handleAccept = () => () => handleStatusChange(editVisible!.id, 'accept');

	return (
		<div className="relative mb-8">
			<EntityContainer.All
				entity="incoming-request"
				name="AllIncoming-Requests"
				url={`/${companyId}/company_connection_requests/`}
				params={{
					page,
					limit: 10,
					extra: {
						search: searchQuery
					}
				}}
			>
				{({ items, isFetched, meta }) => {
					return (
						<>
							{!isFetched ? (
								<Loader tiled withOverlay />
							) : (
								<>
									<TableContainer classes={{ root: 'relative ' }}>
										<StyledTable
											aria-label="simple table"
											classes={{ root: 'table-fixed dark-mode-styled-table' }}
										>
											<TableHead>
												<StyledTableRow>
													<StyledTableHeadCell style={{ width: '30%' }}>
														{t('requesting_business')}
													</StyledTableHeadCell>
													<StyledTableHeadCell style={{ width: '30%' }}>
														{t('connection_type')}
													</StyledTableHeadCell>
													<StyledTableHeadCell style={{ width: '10%' }}>
														{t('status')}
													</StyledTableHeadCell>
													<StyledTableHeadCell style={{ width: '30%', textAlign: 'center' }}>
														{t('actions')}
													</StyledTableHeadCell>
												</StyledTableRow>
											</TableHead>
											<TableBody>
												{(items as IncomingRequestType[]).map((incomingRequest) => {
													const { id, connection_type } = incomingRequest;
													return (
														<StyledTableRow key={id}>
															<StyledTableCell>
																{getRequestClientName(incomingRequest)}
															</StyledTableCell>
															<StyledTableCell>{connection_type}</StyledTableCell>
															<StyledTableCell>Pending</StyledTableCell>
															<StyledTableCell style={{ textAlign: 'center' }}>
																<KntButton
																	isDisabled={!isOwner}
																	className={clsx('mr-4 bg-kred-pink text-white ', {
																		'hover:bg-kred-pink-200': isOwner
																	})}
																	onClick={() => showRejectVisible(incomingRequest)}
																>
																	{t('reject')}
																</KntButton>
																<KntButton
																	isDisabled={!isOwner}
																	color="primary"
																	onClick={() => showEditVisible(incomingRequest)}
																>
																	{t('accept')}
																</KntButton>
															</StyledTableCell>
														</StyledTableRow>
													);
												})}
											</TableBody>
										</StyledTable>
									</TableContainer>
									<ConfirmModal
										featureHandler={handleReject()}
										isOpen={Boolean(rejectVisible)}
										toggleHandler={closeRejectPopup}
										title={t('reject_request_confirmation')}
										hasLoader
										isLoading={rejectLoading}
									/>

									<ConfirmModal
										featureHandler={handleAccept()}
										isOpen={Boolean(editVisible)}
										toggleHandler={closeEditPopup}
										title={t('accept_request_confirmation')}
										hasLoader
										isLoading={editLoading}
									/>
									<div className="news__pagination my-4">
										{items.length ? (
											<Pagination
												count={meta?.last_page}
												page={page ? +page : 1}
												defaultPage={6}
												siblingCount={1}
												color={'primary'}
												onChange={handlePage}
											/>
										) : null}
									</div>
								</>
							)}
						</>
					);
				}}
			</EntityContainer.All>
		</div>
	);
}
