import React from 'react';
import { useAppState } from 'shared/state';
import { usePagination } from 'shared/hooks';
import {
	KntButton,
	Pagination,
	TableBody,
	TableContainer,
	TableHead
} from 'shared/components/Elements';
import EntityContainer from 'modules/entity/containers';
import {
	ActionsButtons,
	ConfirmModal,
	Loader,
	StyledTable,
	StyledTableCell,
	StyledTableHeadCell,
	StyledTableRow
} from 'shared/components';
import { OutgoingRequestType } from 'shared/typings/requests.type';
import { getRequestClientName } from '../../../utils/getRequestClientName';
import { useOutgoingRequests } from '../../../hooks';
import { useTranslation } from 'react-i18next';

type Props = {
	searchQuery: string;
};

export function OutgoingRequests({ searchQuery }: Props) {
	const { t } = useTranslation();
	const { companyId, isPermitted } = useAppState();
	const { page, handlePage } = usePagination();

	const {
		deleteVisible,
		showDeleteVisible,
		deleteLoading,
		closeDeletePopup,
		resendVisible,
		showResendVisible,
		resendLoading,
		closeResendPopup,
		handleDeleteRequest,
		handleResendRequest
	} = useOutgoingRequests();

	const handleDelete = () => () => handleDeleteRequest(deleteVisible!);
	const handleResend = () => () => handleResendRequest(resendVisible!);

	return (
		<div className="relative mb-8">
			<EntityContainer.All
				entity="outgoing-request"
				name="AllOutgoing-Requests"
				url={`/${companyId}/my_company_connection_requests/`}
				params={{
					page,
					limit: 10,
					extra: {
						search: searchQuery
					}
				}}
			>
				{({ items, isFetched, meta }) => {
					return (
						<>
							{!isFetched ? (
								<Loader tiled withOverlay />
							) : (
								<>
									<TableContainer classes={{ root: 'relative ' }}>
										<StyledTable
											aria-label="simple table"
											classes={{ root: 'table-fixed dark-mode-styled-table' }}
										>
											<TableHead>
												<StyledTableRow>
													<StyledTableHeadCell style={{ width: '60%' }}>
														{t('requested_business_connection')}
													</StyledTableHeadCell>
													<StyledTableHeadCell style={{ width: '10%' }}>
														{t('status')}
													</StyledTableHeadCell>
													<StyledTableHeadCell style={{ width: '30%', textAlign: 'center' }}>
														{t('action')}
													</StyledTableHeadCell>
												</StyledTableRow>
											</TableHead>
											<TableBody>
												{(items as OutgoingRequestType[]).map((outgoingRequest) => {
													const { id } = outgoingRequest;
													return (
														<StyledTableRow key={id}>
															<StyledTableCell>
																{getRequestClientName(outgoingRequest, 'requested_company')}
															</StyledTableCell>
															<StyledTableCell>{t('pending')}</StyledTableCell>
															<StyledTableCell style={{ textAlign: 'center' }}>
																<div className="flex-centered">
																	<ActionsButtons.DeleteButton
																		isDisabled={!isPermitted}
																		clickHandler={() => showDeleteVisible(outgoingRequest)}
																	/>
																	<KntButton
																		color="primary"
																		onClick={() => showResendVisible(outgoingRequest)}
																	>
																		{t('resend')}
																	</KntButton>
																</div>
															</StyledTableCell>
														</StyledTableRow>
													);
												})}
											</TableBody>
										</StyledTable>
									</TableContainer>
									<ConfirmModal
										featureHandler={handleDelete()}
										isOpen={Boolean(deleteVisible)}
										toggleHandler={closeDeletePopup}
										title={t('delete_request_confirmation')}
										hasLoader
										isLoading={deleteLoading}
									/>

									<ConfirmModal
										featureHandler={handleResend()}
										isOpen={Boolean(resendVisible)}
										toggleHandler={closeResendPopup}
										title={t('resend_request_confirmation')}
										hasLoader
										isLoading={resendLoading}
									/>
									<div className="news__pagination my-4">
										{items.length ? (
											<Pagination
												count={meta?.last_page}
												page={page ? +page : 1}
												defaultPage={6}
												siblingCount={1}
												color={'primary'}
												onChange={handlePage}
											/>
										) : null}
									</div>
								</>
							)}
						</>
					);
				}}
			</EntityContainer.All>
		</div>
	);
}
