import React, { useCallback } from 'react';
import { CustomMuiTabs, ErrorBoundary } from 'shared/components';

import { Link } from 'react-router-dom';
import { useClientRequests } from '../../hooks';
import { IncomingRequests, OutgoingRequests } from './pages';
import { ListHeader } from '../../../../shared/components/Elements';
import { useSearchQuery } from '../../../../shared/hooks';
import { useTranslation } from 'react-i18next';

function a11yProps(index: number) {
	return {
		id: `request-tabpanel-${index}`,
		'aria-controls': `request-tab-${index}`
	};
}

function ClientsRequestsInner() {
	const { t } = useTranslation();
	const { handleChangeTab, tab } = useClientRequests();

	const { query, setQuery, searchQuery } = useSearchQuery();
	const setQueryHandler = useCallback(
		(event: React.ChangeEvent<HTMLTextAreaElement | HTMLInputElement>) =>
			setQuery(event.target.value),
		[]
	);

	return (
		<>
			<div className="flex-center-between bg-kgrey-bg dark:bg-kdark-lighter">
				<div className="request-tabs prime-tabs mt-4">
					<CustomMuiTabs.Tabs
						roundedTop
						value={tab}
						// @ts-ignore
						onChange={handleChangeTab}
						aria-label="simple tabs"
					>
						<CustomMuiTabs.Tab
							label={t('income')}
							component={Link}
							to={`/clients-management/requests/incoming/`}
							{...a11yProps(0)}
						/>
						<CustomMuiTabs.Tab
							label={t('outgoing')}
							component={Link}
							to={`/clients-management/requests/outgoing/`}
							{...a11yProps(1)}
						/>
					</CustomMuiTabs.Tabs>
				</div>
				<div className="search-request">
					<ListHeader
						title=""
						query={query}
						placeholder={t('search_for_requests')}
						handleSearch={setQueryHandler}
					/>
				</div>
			</div>

			<div>
				{tab === 0 ? (
					<>
						<IncomingRequests searchQuery={searchQuery} />
					</>
				) : (
					<>
						<OutgoingRequests searchQuery={searchQuery} />
					</>
				)}
			</div>
		</>
	);
}

export default function ClientsRequests() {
	return (
		<ErrorBoundary>
			<ClientsRequestsInner />
		</ErrorBoundary>
	);
}
